import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route, HashRouter } from "react-router-dom";
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { MyFieldActivities } from './components/MyFieldActivities/MyFieldActivities';
import { NewActivityForm } from './components/NewActivityForm/NewActivityForm';
import { ActivityList } from './components/ActivityList/ActivityList';
import { Home } from './components/Home/Home'
import { CameraTest } from './components/CameraTest/CameraTest'

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <HashRouter>
    <Routes>
      <Route path="/" element={<App />}>
      <Route path="/react-pwa" element={<App />} />
        <Route index element={<Home />} />
        <Route path="/activities" element={<MyFieldActivities />} >
          <Route index element={<ActivityList />} />
          <Route path="new" element={<NewActivityForm />} />
        </Route>
      </Route>
    </Routes>
    </HashRouter>
  </React.StrictMode>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
